.new-expence {
    display: flex;
    flex-direction: column;
    background-color: #a892ee;
    padding: 1rem;
    margin: 2rem auto;
    margin-bottom: 100px;
    width: 40%;
    max-width: 50%;
    border-radius: 12px;
    text-align: left;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.new-expence button{
    font: inherit;
    cursor: pointer;
    padding: 1rem 2rem;
    border: 1pz solid #40005d ;
    background-color: #40005d;
    color: white;
    border-radius: 12px;
    margin-right: 1rem;
}

.new-expence button:hover,
.new-expence button:active{
    background-color: #510674;
    border-color: #510674;
}

.new-expence button.alternative{
    color: #220131;
    border-color: transparent;
    background-color: transparent;
}

.new-expence button.alternative:hover,
.new-expence button.alternative:active{
    background-color: #ddb3f8;
}
